body {
  display: flex;
  flex-direction: column;
  background-color: #fcf5da;

  * {
    display: flex;
    flex-direction: column;
  }
}

* {
  margin: 0;
  padding: 0;
}

.front {
  width: 100vw;
  height: 100vh;

  align-items: center;
  justify-content: center;
}

h1 {
  font-size: 10vw;
  padding: 10px 20px;
}

p {
  font-size: 24px;
  padding: 10px 20px;
}

h2 {
  font-size: 5vw;
  padding-left: 10px;
}

.border {
  border: 2px solid black;
  margin: 10px;
  padding: 20px
}

.main {
  align-items: center;
}

.intro-text {
  border-left: 2px solid black;
  margin: 10px 20px;
}

.flex-width {
  width: 100%;
  display: flex;
}
